#gallery {
    width:100%; 
    height: 310vh;
    padding: 50px 0px;

    .gallery__inner{
    width:1180px; 
    margin:0 auto; 
        .gallery__top__txt{
            height: 50px;
            border-bottom: 1px solid var(--color-gray-01);
            margin-bottom: 30px;
            h1{
                font:20px 'open sans';
                color:var(--color-darkgray-01);
                font-weight: 300;
                padding-bottom: 20px;
                letter-spacing: 1px;
                margin-left:1030px;
            }
        }

         .gallery__wrap{
            >section{
            &::after{
                content:""; display: block; clear : both;
            }

                article {
                    width: 25%; height: 300px; float:left; padding:20px;

                    >.inner{
                        width: 100%; height: 100%; background:#fff;
                        box-shadow: 5px 5px 10px var(--color-gray-00); padding: 18px;

                        .pic {
                            width: 100%; height: 180px; margin-bottom: 20px; 

                            img {
                                width: 100%; height: 100%; object-fit: cover;
                            }
                        }

                        h2 {
                            font: normal 14px/1.2 "dosis"; color:#777;
                            text-align: center;
                        }
                    }
                }
            }
        }
    }

   
}
.pop {
    width: 100%; height:100vh; position: fixed; top: 0px; left: 0px; background: var(--color-darkgray-03); padding: 5vw; 
    z-index:6;

    img {
        width: 100%; height: 100%; object-fit:contain;
    }
    p {
        font: 14px/1.2 "dosis"; color: var(--color-white-00); text-align: center;
    }

    span {
        font: bold 14px/1 "dosis"; color: var(--color-white-00);
        position: absolute; top: 3vw; right: 5vw;
        cursor: pointer;
    }    
}   